import React from "react"
import { useHistory,useRouteMatch } from 'react-router-dom'
import style from './Memos.module.css'

// import Article from './articles/articles.js'
// import E1 from './Text/e1.js'
function Article(props){
  let history = useHistory()
  let { url } = useRouteMatch();
  function jumpto(){
    history.push(`${url}${props.path}`)
  }
  return (
    <div className={style.items} onClick={jumpto}>
      <div className={style.content}>
        {props.content}
      </div>
      <div className={style.title}>
        {props.title}
      </div>
    </div>
  )
}

function F1(props){
  let articles = props.articles
  let articlesNode = articles.map((item, index)=>
    // <div key={index} className={style.items}>{str}</div>
    <Article key={index} title={item.title} path={item.path}></Article>
  )
  return articlesNode
}

function Memo(){
  let articles = [
    {title:'外语学习', path:'/ForeignLanguages'},
    {title:'力扣解题法', path:'/Leetcode'},
    {title:'软件与自由', path:'/softwareAndFreedom'}
  ]
  return (
    <div>
      <div className={style.App}>
        <F1 articles={articles}></F1>
      </div>
    </div>
  )
}
export default Memo